import axios from 'axios'

//  **Storage storageAvailable
function storageAvailable (type) {
  var storage
  try {
    storage = window[type]
    var x = '__storage_test__'
    storage.setItem(x, x)
    storage.removeItem(x)
    return true
  } catch (e) {
    return e instanceof DOMException && (
    // everything except Firefox
      e.code === 22 ||
          // Firefox
          e.code === 1014 ||
          // test name field too, because code might not be present
          // everything except Firefox
          e.name === 'QuotaExceededError' ||
          // Firefox
          e.name === 'NS_ERROR_DOM_QUOTA_REACHED') &&
          // acknowledge QuotaExceededError only if there's something already stored
          (storage && storage.length !== 0)
  }
}

// 
let backIndex = ''
let backData = []

// localStorecacheIndexstorageAvailable sessionStorage 
function localStore (response, timeDays) {
  if (storageAvailable('sessionStorage')) {
    let cacheIndex = '' // sessionStorage{cacheIndex:cacheData}
    let cacheData = [] // [{expiretime:,response:response}]
    let cacheObj = {} // {expiretime:,response:response}
    let expiretime = 0 // 

    // timeDays
    if (parseFloat(timeDays) < 0) {
      expiretime = Date.now() // 
    } else if (parseFloat(timeDays) < 1) {
      expiretime = Date.now() + 24 * 60 * 60 * 1000 * parseFloat(timeDays) // < 1d
    } else {
      expiretime = Date.now() + 24 * 60 * 60 * 1000 * Math.round(timeDays) // >= 1d
    }

    // 200
    if (response.status === 200) {
      cacheIndex = JSON.stringify(response.config.url + response.config.method + JSON.stringify(response.config.params)) // url
      // [{expiretime:,response:response}]
      cacheObj.expiretime = expiretime
      cacheObj.response = response
      cacheData.push(cacheObj)
      // 
      sessionStorage.setItem(cacheIndex, JSON.stringify(cacheData))
    }
  }
  return response
}

// curReqIndex
function cacherules (params) {
  // 
  const CancelToken = axios.CancelToken
  const source = CancelToken.source()

  // url
  let curReqIndex = JSON.stringify(params.url + params.method + JSON.stringify(params.params))

  // url+ = 
  if (sessionStorage.getItem(curReqIndex)) {
    let pastData = JSON.parse(sessionStorage.getItem(curReqIndex)) // [{expiretime:,response:params}]
    let cacheExTime = [] // expiretime - 

    // (>0)(max)
    pastData.forEach(el => {
      cacheExTime.push(new Date(el.expiretime) - Date.now())
    })
    if (Math.max(...cacheExTime) > 0) {
      // cancelToken
      params.cancelToken = source.token
      // 
      backIndex = cacheExTime.indexOf(Math.max(...cacheExTime))
      backData = pastData[backIndex].response

      // source.cancel()error.message200
      source.cancel(JSON.stringify(backData))
    } else {
      sessionStorage.removeItem(curReqIndex) // 
    }
  }
  return params
}

export {localStore, cacherules}



