<template>
  <div id="work">
    <titleBorder></titleBorder>
    <div id="urban">
      <div>
        <p> {{ curWea.area }} </p>
        <p>
          <button @click="showSelector = !showSelector">  </button>
          <citySelector @change="updatedArea" innertext="" v-if="showSelector" class="cityPosition"></citySelector>
        </p>
        <p>{{ curWea.province }}</p>
      </div>
      <div>
        <p></p>
        <p><i class="iconfont" :class="weatherIcon"></i></p>
        <p>{{ curWea.weather }}</p>
      </div>
      <div>
        <p></p>
        <p>{{ removeUnit(curWea.real) }}<sup>&#x2103;</sup></p>
        <p id="temRank">{{ removeUnit(curWea.lowest) }} ~ <span>{{ removeUnit(curWea.highest) }}</span> &#x2103;</p>
      </div>
      <div>
        <p></p>
        <p><i class="el-icon-stopwatch"></i></p>
        <p>{{ airRank(curWea.quality) }}</p>
      </div>
      <div>
        <p></p>
        <p><i class="el-icon-view"></i></p>
        <p>{{curWea.vis}} km</p>
      </div>
      <div>
        <p></p>
        <p><i class="el-icon-umbrella"></i></p>
        <p>{{ uvRank(curWea.uv_index) }}</p>
      </div>
      <div>
        <p></p>
        <p>{{ curWea.wind }}</p>
        <p>{{ curWea.windsc }}</p>
      </div>
      <div>
        <p></p>
        <div>
          <span v-for="(alarm, index) in hasAlarm(curWea.alarmlist)" :key="index">
            <el-popover
                placement="left-end"
                title=""
                width="300"
                trigger="hover"
                :content="alarm.level + alarm.type + '' + alarm.content">
                <el-button slot="reference" :style="alarmColor(alarm.level)">
                  {{ alarm.level }}{{ alarm.type }}
                </el-button>
              </el-popover>
          </span>
        </div>
      </div>
    </div>
    <nav>
      <router-link :to="{name: 'work', params: {city: areaid}}" :class="routeHome">
        <span class="el-icon-pie-chart"></span> 
      </router-link>
      <router-link :to="{name: 'daysWea', params: {city: areaid}}" :class="routeDays">
        <span class="el-icon-data-line"></span> 
      </router-link>
    </nav>
    <div id="routerView">
      <countyWeaVue v-if="active" :initialid="areaid"></countyWeaVue>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import titleBorder from './title.vue'
import citySelector from '../public/citySelect.vue'
import countyWeaVue from './countyWea.vue'
import api from '../../utils/index'

export default {
  components: {titleBorder, citySelector, countyWeaVue},
  beforeRouteEnter (to, from, next) {
    api.getTWea1(to.params.city).then(res => {
      next(vm => {
        vm.curWea = res.data.result
        vm.areaid = to.params.city
      })
    })
      .catch(() => {
        alert('')
        next(vm => { vm.areaid = to.params.city })
      })
  },
  beforeRouteUpdate (to, from, next) {
    api.getTWea1(to.params.city).then(res => {
      this.curWea = res.data.result
      next()
    })
      .catch(() => {
        alert('')
        next()
      })
    this.areaid = to.params.city
    to.path.includes('daysWea') ? this.active = false : this.active = true
  },
  data () {
    return {
      active: true, // 
      showSelector: false, // 
      areaid: '101280101',
      curWea: {
        'date': '2020-05-23',
        'week': '',
        'province': '',
        'area': '',
        'areaid': '101020100',
        'weather': '',
        'weatherimg': 'duoyun.png',
        'weathercode': 'qing_duoyun',
        'real': '20',
        'lowest': '6',
        'highest': '22',
        'wind': '',
        'windspeed': '7',
        'windsc': '1-2',
        'sunrise': '06:10',
        'sunset': '22:31',
        'moonrise': '06:02',
        'moondown': '22:22',
        'pcpn': '0.0',
        'uv_index': '0',
        'aqi': '34',
        'quality': '',
        'vis': '25',
        'humidity': '23',
        'alarmlist': [
          {
            'province': '',
            'city': '',
            'level': '',
            'type': '',
            'content': '202005232030500200',
            'time': '2020-05-23 20:30:00'
          }
        ],
        'tips': 'T'
      }// 
    }
  },
  computed: {
    // 
    removeUnit () {
      return (val) => {
        if (typeof val === 'string') {
          let realTemp = val.split('')
          realTemp.pop()
          realTemp = Number(realTemp.join(''))
          return realTemp
        } else {
          return val
        }
      }
    },
    // 
    alarmColor () {
      return val => {
        if (val === '') {
          return {background: 'red'} // 
        } else if (val === '') {
          return {background: 'orange'} // 
        } else if (val === '') {
          return {background: 'yellow'} // 
        } else if (val === '') {
          return {background: 'blue'} // 
        } else {
          return {background: '#42b883'} // 0
        }
      }
    },
    // 
    uvRank () {
      return val => {
        if (val < 3) {
          return ''
        } else if (val < 5) {
          return ''
        } else if (val < 7) {
          return ''
        } else if (val < 11) {
          return ''
        } else {
          return ''
        }
      }
    },
    // 
    airRank () {
      return val => {
        if (parseInt(val) < 51) {
          return ''
        } else if (parseInt(val) < 101) {
          return ''
        } else if (parseInt(val) < 151) {
          return ''
        } else if (parseInt(val) < 201) {
          return ''
        } else if (parseInt(val) < 301) {
          return ''
        } else {
          return ''
        }
      }
    },
    // 
    weatherIcon () {
      if (this.curWea.weatherimg) {
        let weathercode = this.curWea.weatherimg.slice(0, -4)
        return `icon-tianqi-${weathercode}`
      } else {
        return `el-icon-star-off`
      }
    },
    // 
    routeHome () {
      if (this.$route.path.includes('daysWea')) {
        return ''
      } else {
        return 'focusColor'
      }
    },
    // 
    routeDays () {
      if (this.$route.path.includes('daysWea')) {
        return 'focusColor'
      } else {
        return ''
      }
    }
  },
  methods: {
    // id
    updatedArea (e) {
      localStorage.setItem('historyAreaid', e)
      setTimeout(() => {
        this.showSelector = false
      }, 1000)
      this.$router.push({name: 'work', params: {city: e}})
    },
    // 
    hasAlarm (params) {
      if (params !== undefined && params.length !== 0) {
        return params
      } else {
        return [{time: '0', content: '', level: '', type: ''}]
      }
    }
  },
  mounted () {
    alert('\n')
  }
}
</script>

<style lang="scss">
// 
$fontColor:rgba(#fff,.87);
$color10:#B2CCD6;
$color11:#A6ACCD;
// 
$subFontColor:rgba(#ebebeb,.6);
$rr:#ffffffde;
// 
$backColor:#1a1a1a;
// 
$subBackColor:#242424;
// 
$mainColor:#42b883;
// 
$color1:#f07178;
$color2:#89DDFF;
$color3:#F78C6C;
$color4:#C792EA;
$color5:#C3E88D;
#work{
  height: 100vh;
  width: 100vw;
  min-width: 1400px;
  overflow: auto;
  background-color: $backColor;
  color:$subFontColor;
  font-size: 16px;
  // 
  #urban{
    padding: 10px;
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: start;
    >div{
      flex: 1;
      margin: 0 10px;
      padding: 0.5rem;
      height: 120px;
      // flex
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-between;
      align-items: center;
      // 
      overflow: auto;
      >p{
        margin: 0;
      }
      // 
      >p:first-child{
        color:$fontColor;
        font-weight: bolder;
      }
      // 
      >p:nth-child(2){
        background: linear-gradient(0deg,$fontColor,$mainColor);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
        font-weight: bolder;
        font-size: 2em;
      }
      // 
      .iconfont{
        font-size: 1.5em;
      }
      // 
      #temRank{
        font-weight: bolder;
        span{
          color: #ebebeb;
        }
      }
      transition: all 0.5s ease;
      // 
      &:hover{
        background: $subBackColor;
        >p:nth-child(2){
          color:$mainColor;
        }
      }
    }
    // 
    >div:first-child,>div:last-child{
      flex: 2;
      margin: 0;
      border-top: 2px solid rgba($mainColor,.8);
      border-bottom: 2px solid rgba($mainColor,.8);
      background: rgba($mainColor,.2);
    }
    // 
    >div:first-child{
      >p:first-child{
        font-size: 2em;
        font-family: cursive;
      }
      >p:last-child{
        font-size: 0.8em;
      }
      //  
      button{
        border:none;
        border-radius: 10px;
        color:$subFontColor;
        background: none;
        box-shadow: inset 0 0 10px 0 $fontColor;
        &:hover{
          background: $mainColor;
          box-shadow: none;
          color:$backColor;
          transition: all 0.5s ease;
        }
      }
    }
    // 
    >div:last-child{
      >p:first-child{
        width: 100%;
        text-align: left;
      }
      // element-ui
      button{
        margin: 2px;
        padding: 5px;
        border: none;
        border-radius: 30px;
        background: transparent;
        font-size: 0.8em;
        font-weight: bolder;
        color: black;
      }
    }
    // 
    position: relative;
    .cityPosition{
      z-index: 99;
      position: absolute;
      left:5%;
      top:70%;
      background: linear-gradient(45deg,$mainColor,$color1);
    }
  }
  // 
  >nav{
    width: 100%;
    background-color: $subBackColor;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    // 
    a{
      display: inline-block;
      margin: 5px 5px 0 5px;
      border-bottom: 2px solid $subBackColor;
      width: 10rem;
      height: 100%;
      text-align: center;
      font-size: 1.25rem;
      font-weight: bolder;
      text-decoration: none;
      transition: all 0.5s 0.1s linear;
      &:link,&:visited{
        color:$fontColor;
      }
      &:active,&:hover,&:focus{
        color:$mainColor;
        border-bottom: 2px solid $mainColor;
      }
    }
    .focusColor{
      color:$mainColor;
      border-bottom: 2px solid $mainColor;
    }
  }
  // 
  #routerView{
    width: calc(100% - 5px);
    overflow: auto;
  }
}
</style>



