<template>
  <div id="personWrap" class="flex" :style="imgback" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="......">
    <button id="changeBack" @click="changeBack($event)" v-useDrag>
      <i class="el-icon-refresh"></i>
    </button>
    <button @click="changeRoute" id="changeRoute"></button>
    <div id="picTip" v-show="loadpic">
      <div v-if="!getpic">......</div>
      <div v-if="getpic"></div>
    </div>
    <div id="currentWea" class="flex blurWrap">
      <p>
        <i class="el-icon-location"></i>{{ curRes.area }}
      </p>
      <p>
        <button @click="showSelector = !showSelector">[]</button>
        <citySelect @change="updatedArea" innertext="" v-show="showSelector" class="cityPosition"></citySelect>
      </p>
      <p><span>{{ parseInt(curRes.real) }}</span></p>
      <p>{{ curRes.lowest }} ~ {{ curRes.highest }}</p>
      <p>{{ curRes.weather }} &nbsp;{{ airRank(curRes.aqi) }}</p>
      <p>{{ curRes.wind }} {{ curRes.windsc }}</p>
      <p>
        <span>
          <el-popover
            placement="top"
            title="tips:"
            width="300"
            trigger="click"
            :content="curRes.tips">
            <el-button slot="reference"></el-button>
          </el-popover>
        </span>
      </p>
      <p v-if="hasalarm">
        <span v-for="(alarm, index) in curRes.alarmlist" :key="index">
          <el-popover
            placement="top"
            title=":"
            width="300"
            trigger="click"
            :content="alarm.content">
            <el-button slot="reference"><i class="el-icon-bell"></i>{{ alarm.level }}{{ alarm.type }}</el-button>
          </el-popover>
        </span>
      </p>
    </div>
  </div>
</template>

<script>
import api from '../../utils/index'
import citySelect from '../public/citySelect.vue'
import pic from '../../assets/json/picture.json'

export default {
  components: {citySelect},
  data () {
    return {
      // 
      fullscreenLoading: false,
      // 
      loadpic: false,
      getpic: true,
      // 
      showSelector: false,
      // 
      iter: 0,
      imgback: {backgroundImage: "url('https://www.dpm.org.cn/Uploads/Picture/2022/05/31/s62956bf17e916.jpg')"},
      // id
      areaid: '',
      // 
      curRes: {
        'date': '2020-05-23',
        'week': '',
        'province': '',
        'area': '',
        'areaid': '101020100',
        'weather': '',
        'weatherimg': 'duoyun.png',
        'weathercode': 'qing_duoyun',
        'real': '20',
        'lowest': '6',
        'highest': '22',
        'wind': '',
        'windspeed': '7',
        'windsc': '1-2',
        'sunrise': '06:10',
        'sunset': '22:31',
        'moonrise': '06:02',
        'moondown': '22:22',
        'pcpn': '0.0',
        'uv_index': '0',
        'aqi': '34',
        'quality': '',
        'vis': '25',
        'humidity': '23',
        'alarmlist': [
          {
            'province': '',
            'city': '',
            'level': '',
            'type': '',
            'content': '202005232030500200',
            'time': '2020-05-23 20:30:00'
          }
        ],
        'tips': 'T'
      }
    }
  },
  computed: {
    hasalarm () {
      if (this.curRes.alarmlist !== undefined && this.curRes.alarmlist.length !== 0) {
        return true
      } else {
        return false
      }
    }
  },
  watch: {
    // id
    areaid: {
      handler (cur) {
        if (cur) {
          this.fullscreenLoading = true
          this.getData(cur)
          setTimeout(() => {
            this.showSelector = false
          }, 2000)
        }
      },
      immediate: true
    },
    // 
    curRes: {
      handler () {},
      immediate: true,
      deep: true
    },
    // 
    imgback: {
      handler (cur) {
        let imgall = cur.backgroundImage
        let imgallArr = imgall.split(',')
        let result = ''
        // 5
        if (imgallArr.length > 5) {
          imgallArr.splice(1, 5)
          result = imgallArr.join(',')
          this.imgback.backgroundImage = result
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    getData (params) {
      api.getTWea1(params).then((res) => {
        this.curRes = res.data.result
        this.fullscreenLoading = false
      })
        .catch(() => {
          alert('')
        })
    },
    // 
    changeBack (event) {
      this.loadpic = true
      this.getpic = false
      if (parseInt(this.iter) > 4) {
        this.iter = 0
        event.target.style.backgroundImage = `none`
      } else {
        this.iter += 1
        event.target.style.backgroundImage = `radial-gradient(circle at 50% 0,transparent ${100 - this.iter * 20}%,#ef5e1f ${this.iter * 20}%)`
      }
      // 
      let imgload = new Promise((resolve, reject) => {
        const img = new Image()
        img.src = pic[this.iter]
        // resolve
        img.onload = resolve(img.src)
        img.onerror = reject
      })
      imgload.then((params) => {
        this.getpic = true
        this.imgback.backgroundImage = `url('${params}'),` + this.imgback.backgroundImage
        this.loadpic = false
      })
        .catch(() => {
          this.loadpic = false
          alert('')
        })
    },
    // id
    updatedArea (e) {
      this.areaid = e
      localStorage.setItem('historyAreaid', e)
    },
    // :AQI
    airRank (params) {
      if (parseInt(params) < 51) {
        return ''
      } else if (parseInt(params) < 101) {
        return ''
      } else if (parseInt(params) < 151) {
        return ''
      } else if (parseInt(params) < 201) {
        return ''
      } else if (parseInt(params) < 301) {
        return ''
      } else {
        return ''
      }
    },
    changeRoute () {
      setTimeout(() => {
        let result = confirm('')
        if (result) {
          this.$router.push({name: 'work', params: {city: localStorage.getItem('historyAreaid')}})
        }
      }, 100)
    }
  },
  created () {
    this.getData(localStorage.getItem('historyAreaid'))
  },
  mounted () {
    alert('\n\n^-^')
  }
}
</script>

<style lang="scss">
$fontColor:#fff;
#personWrap{
  @media screen and (max-width : 900px) {
    font-size: 16px;
  }
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  font-size: 24px;
  font-weight: 600;
  color: $fontColor;
  background: no-repeat center/cover fixed;
  background-image: url('https://www.dpm.org.cn/Uploads/Picture/2022/05/31/s62956bf17e916.jpg');
  background-color: #c06c84;
  transition: background-image 1.5s linear;
  // 
  #changeBack{
    position: fixed;
    left:80%;
    top:80%;
    z-index: 99;
    width: 50px;
    height: 50px;
    border:1px solid transparent;
    border-radius: 50%;
    background: none;
    box-shadow:inset 0 0 10px 0 rgb(202, 200, 200);
    transition: background-image,box-shadow 1s ease-in;
    i{
      font-size: 2em;
      color:#fff;
    }
    // 
    &:hover{
      background-image: radial-gradient(rgb(15, 15, 15) 1%,transparent);
      i{
        display: none;
      }
      &::before{
        content:'';
        width: 60px;
        height: 60px;
        color:#fff;
      }
    }
    // 
    &:active{
      background-image: radial-gradient(#ebebeb 10%,transparent);
      &::before{
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        content:'';
        width: 50px;
        height: 50px;
        border-radius: 50%;
        border: 5px solid #ebebeb;
        animation: halo 1s infinite alternate linear;
        @keyframes halo {
          50%{
            width: 60px;
            height: 60px;
          }
          100%{
            width: 65px;
            height: 65px;
            border-width: 2px;
          }
        }
      }
    }
  }
  .flex{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    align-items: center;
  }
  // 
  .blurWrap{
    margin:15vh auto;
    width: calc(100% - 5px);
    height: 70%;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
  }
  // 
  #currentWea{
    padding: 10px 0;
    text-align: center;
    // 
    position: relative;
    p{
      margin: 10px;
      // 
      &:first-child{
        margin-bottom: 0;
        font-size: 1.5em;
        font-family: cursive;
      }
      // 
      &:nth-child(2){
        margin: 0 auto;
        button{
          border: none;
          background: none;
          color:inherit;
          font-size: 0.5em;
          font-weight: bold;
          &:hover{
            color:#114193;
          }
        }
        .cityPosition{
          position: absolute;
          top:50%;
          left:50%;
          transform: translate(-50%,-50%);
          height: 15rem;
          background: linear-gradient(45deg,#355c7d,#6c5b7b,#c06c84);
        }
      }
      // 
      &:nth-child(3) span{
        font-size: 3.5em;
      }
    }
  }
  // 
  #changeRoute{
    position: fixed;
    bottom: 10px;
    background: rgba($fontColor, 0.8);
    border:none;
    outline: none;
    opacity:0.8;
  }
}
</style>



