<template>
  <div id="daysWea"></div>
</template>

<script>
import deOption from '../../utils/option'
import api from '../../utils/index'

export default {
  beforeRouteEnter (to, from, next) {
    api.getYWea7(to.params.city).then((res) => {
      if (res.data.errcode) {
        return Promise.reject(res.data.errmessage)
      } else {
        next(vm => {
          vm.daysWea = res.data
        })
      }
    })
      .catch(() => {
        alert('\n')
        next()
      })
  },
  data () {
    return {
      areaid: '', // id
      daysWea: [
        {
          'date': '2023-03-19',
          'wea': '',
          'wea_img': 'qing',
          'tem_day': '47',
          'tem_night': '4',
          'win': '',
          'win_speed': '3-4'
        }
      ] // 
    }
  },
  computed: {
    // x
    datesArr () {
      let result = []
      let list = Array.from(this.daysWea.data)
      if (list !== undefined) {
        list.forEach((params) => {
          result.push(params.date) // 
        })
      }
      return result
    }
  },
  methods: {
    // echarts
    drawChart () {
      // 
      let diyOption = {
        dataset: {
          dimensions: ['date', 'tem_day', 'tem_night', 'wea', 'wea_img', 'win', 'win_speed'],
          source: this.daysWea.data
        },
        // x
        xAxis: [
          // x:date
          {
            id: 'date',
            type: 'category',
            data: this.datesArr,
            axisLine: {
              show: false, // 
              onZero: false // offset
            },
            axisTick: {
              show: true,
              inside: true,
              alignWithLabel: true
            },
            axisLabel: {
              show: true,
              color: 'inherit',
              fontSize: '1rem',
              fontWeight: 'bold',
              formatter: params => params.slice(5)
            },
            axisPointer: {
              show: true,
              type: 'line',
              lineStyle: {
                color: 'inherit'
              }
            }
          },
          // x:week
          {
            id: 'week',
            type: 'category',
            data: this.datesArr,
            axisLine: {
              show: false, // 
              onZero: false // offset
            },
            offset: 25,
            position: 'bottom',
            axisTick: {
              show: false // 
            },
            axisLabel: {
              show: true,
              color: 'inherit',
              fontSize: '0.8rem',
              formatter: function (params) {
                let date = new Date(params)
                let weekzh = ['', '', '', '', '', '', '']
                return weekzh[date.getDay()]
              }
            }
          }
        ],
        series: [
          // 
          {
            name: '',
            type: 'line',
            encode: {
              x: 'date',
              y: 'tem_night'
            },
            smooth: true,
            lineStyle: {
              width: 3
            },
            label: {
              show: true,
              position: 'bottom',
              color: 'inherit',
              fontSize: '1rem',
              fontWeight: 600,
              formatter: '{@tem_night}'
            },
            symbolSize: 6,
            markPoint: {
              symbolSize: 10,
              symbol: 'circle',
              label: {
                show: false
              },
              data: [
                {
                  name: '',
                  type: 'min'
                }
              ]
            },
            markLine: {
              symbol: 'none',
              data: [
                {
                  name: '40',
                  yAxis: 40,
                  lineStyle: {color: 'red'},
                  label: {
                    formatter: '{b}',
                    color: 'inherit'
                  }
                },
                {
                  name: '-25',
                  yAxis: -25,
                  lineStyle: {color: 'red'},
                  label: {
                    formatter: '{b}',
                    color: 'inherit'
                  }
                },
                {
                  name: '37',
                  yAxis: 37,
                  lineStyle: {color: 'orange'},
                  label: {
                    formatter: '{b}',
                    color: 'inherit'
                  }
                },
                {
                  name: '-20',
                  yAxis: -20,
                  lineStyle: {color: 'orange'},
                  label: {
                    formatter: '{b}',
                    color: 'inherit'
                  }
                },
                {
                  name: '35',
                  yAxis: 35,
                  lineStyle: {color: 'yellow'},
                  label: {
                    formatter: '{b}',
                    color: 'inherit'
                  }
                },
                {
                  name: '-15',
                  yAxis: -15,
                  lineStyle: {color: 'yellow'},
                  label: {
                    formatter: '{b}',
                    color: 'inherit'
                  }
                },
                {
                  name: '-10',
                  yAxis: -10,
                  lineStyle: {color: 'blue'},
                  label: {
                    formatter: '{b}',
                    color: 'inherit'
                  }
                }
              ]
            }
          },
          // 
          {
            name: '',
            type: 'line',
            encode: {
              x: 'date',
              y: 'tem_day'
            },
            smooth: true,
            lineStyle: {
              width: 3
            },
            areaStyle: {
              origin: 'start',
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0, color: '#f07178'
                  },
                  {
                    offset: 0.5, color: 'rgba(240,113,120,0.5)'
                  },
                  {
                    offset: 0.7, color: 'rgba(240,113,120,0.2)'
                  },
                  {
                    offset: 1, color: 'rgba(240,113,120,0.01)'
                  }
                ],
                global: false //  false
              }
            },
            label: {
              show: true,
              position: 'top',
              color: 'inherit',
              fontSize: '1rem',
              fontWeight: 600,
              formatter: '{@tem_day}'
            },
            symbolSize: 6,
            markPoint: {
              symbolSize: 10,
              symbol: 'circle',
              label: {
                show: false
              },
              data: [
                {
                  name: '',
                  type: 'max'
                }
              ]
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '/C',
            nameTextStyle: {
              align: 'right',
              color: 'rgba(235,235,235,.6)'
            },
            // 
            axisLabel: {
              formatter: '{value} C'
            },
            max: 'dataMax',
            min: function (value) {
              return value.min - 5
            },
            // y
            splitLine: {
              show: true,
              lineStyle: {color: 'rgba(235,235,235,.4)'}
            }
          }
        ],
        // 
        tooltip: {
          formatter: function (params) {
            let diffDate = new Date() - new Date(params[0].name) // data
            let totalDays = Math.floor(diffDate / (1000 * 60 * 60 * 24)) // 10-1
            if (totalDays === 0) {
              return `<div style="text-align:justify;"><br>${params[0].value.wea}<br>${params[0].value.tem_day}<br>${params[0].value.tem_night}</div>` // htmlstyle
            } else if (totalDays === -1) {
              return `<div style="text-align:justify;"><br>${params[0].value.wea}<br>${params[0].value.tem_day}<br>${params[0].value.tem_night}</div>`
            } else {
              return `<div style="text-align:justify;">${params[0].value.date.slice(5)}<br>${params[0].value.wea}<br>${params[0].value.tem_day}<br>${params[0].value.tem_night}</div>`
            }
          }
        },
        title: {
          text: `${this.daysWea.city}`,
          textStyle: {
            color: 'rgba(255,255,255,0.87)'
          },
          top: 10
        }
      }
      // 
      let chart = this.$echarts.init(document.getElementById('daysWea'))
      chart.setOption(deOption)
      chart.setOption(diyOption)
    }
  },
  watch: {
    daysWea: {
      handler (cur) {
        if (cur) {
          this.$nextTick(() => {
            this.drawChart()
          })
        }
      },
      // 
      immediate: true
    }
  },
  mounted () {
    this.drawChart()
    visualViewport.addEventListener('resize', this.drawChart)
  },
  beforeDestroy () {
    visualViewport.removeEventListener('resize', this.drawChart)
  }
}
</script>

<style lang="scss">
#daysWea{
  margin:10px auto;
  width: 1200px;
  height: 500px;
  color:inherit;
}
</style>



