<template>
    <div id="citySelector">
      <label for="proSelect">{{ innertext }}</label>
      <!--  -->
      <select v-model="proArea">
        <option disabled value="">----</option>
        <option v-for="province in cityallData"
          :key="province.provinceName"
          :value="province.provinceName">
          {{ province.provinceName }}
        </option>
      </select>
      <!--  -->
      <select v-model="cityArea" v-on:change="sendID">
        <option disabled value="">----</option>
        <option v-for="city in cityData" :key="city.id" :value="city.id">{{ city.cityZh }}</option>
      </select>
    </div>
</template>

<script>
import all from '../../assets/json/allcities.json'

export default {
  props: {
    innertext: {
      type: String,
      default: function () {
        return ''
      }
    }
  },
  data () {
    return {
      proArea: '', // **
      cityData: [], // **
      cityArea: '', // **
      countyData: [], // **
      cityallData: all // 
    }
  },
  watch: {
    // **
    proArea: function (cur) {
      let that = this
      if (all.length > 0) {
        let temp = Array.from(all)
        temp.forEach((pro) => {
          if (pro.provinceName === cur) {
            that.cityData = pro.cities
          }
        })
      }
    }
  },
  methods: {
    // 
    sendID (e) {
      this.$emit('change', e.target.value)
    }
  }
}
</script>

<style lang="scss">
// 
#citySelector{
  border: 1px solid #ebfcff;
  border-radius: 10px;
  padding: 10px;
  width: fit-content;
  height: fit-content;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  font-size: 1rem;
  font-weight: bold;
  // 
  select{
    margin:1rem 0;
    width: 10rem;
    height: 1.5rem;
    border: 2px solid #ebfcff;
    border-radius: 10px;
    color: #000;
  }
}
</style>



